<template>
  <div class="page">
    <div class="adduser">
      <q-input v-model="userId" label="Kullanıcı Adı" />

      <q-input v-model="userPass" label="Kullanıcı Şifre" />

      <q-radio v-model="userType" val="Admin" label="Admin" />
      <q-radio v-model="userType" val="User" label="User" />
      <q-radio v-model="userType" val="Sil" label="Sil" />

      <q-btn
        class="right"
        color="white"
        text-color="black"
        label="Ekle / Güncelle"
        @click="addProfileFuncBegin"
      />
    </div>

    <q-table
      title="Kullanıcılar"
      :rows="users"
      :columns="columns"
      row-key="name"
    />
  </div>
</template>

<script>
import {
  addProfile,
  setProfile,
  getProfiles,
  delProfiles,
  readCookie,
} from "@/services.js";

const columns = [
  {
    name: "user",
    label: "User",
    align: "left",
    field: (row) => row.user,
    sortable: true,
  },
  { name: "pass", label: "Password", field: "pass", sortable: true },
  {
    name: "type",
    label: "Type",
    field: "type",
    align: "center",
    sortable: true,
  },
];

export default {
  data() {
    return {
      page: "admin",
      userId: null,
      userPass: null,
      userType: "User",
      columns: columns,
      users: [],
    };
  },
  methods: {
    chooseType: function (value) {
      this.userType = value.target.innerText;
    },
    addProfileFuncBegin() {
      if (this.userType != "Sil") {
        if ((this.userId != null) & (this.userPass != null)) {
          this.addProfileFunc();
        }
      } else {
        this.delProfileFunc();
      }
    },
    addProfileFunc: function () {
      addProfile(this.page, this.userId, this.userPass, this.userType)
        .then(() => {
          this.getProfilesFunc();
        })
        .catch(() => {
          setProfile(this.page, this.userId, this.userPass, this.userType).then(
            () => {
              this.getProfilesFunc();
            }
          );
        });
    },
    delProfileFunc: function () {
      delProfiles(this.page, this.userId).then(() => {
        this.getProfilesFunc();
      });
    },
    getProfilesFunc: function () {
      this.users = [];
      getProfiles(this.page).then((res) => {
        res.forEach((element) => {
          const data = {
            user: element.get("email"),
            pass: element.get("password"),
            type: element.get("type"),
          };
          this.users.push(data);
        });
      });
    },
  },
  watch: {
    userId() {
      document.cookie = `userId2=${this.userId}; SameSite=Lax; Secure`;
    },
  },
  mounted() {
    this.getProfilesFunc();
    this.userId = readCookie("userId2");
  },
};
</script>


<style scoped>
.adduser {
  display: flex;
  flex-wrap: wrap;
}
.adduser > *:not(.q-radio) {
  display: block;
  width: 100%;
}
.q-radio {
  width: 33%;
}
.right {
  margin-right: 0px;
  margin-left: auto;
}
</style>a