<template>
  <loginPage v-if="!isLogged" @eventLogin="eventLogin"></loginPage>
  <otelPage v-if="isLogged && userType == 'Otel'" @logout="logout" :user="user"></otelPage>
  <turPage v-if="isLogged && userType == 'Tur'" @logout="logout" :user="user"></turPage>
  <userPage v-if="isLogged && userType == 'User'" @logout="logout" :user="user"></userPage>
  <adminPage v-if="isLogged && userType == 'Admin'" @logout="logout" :user="user"></adminPage>
</template>

<script>
import loginPage from "@/mainPages/LoginPage.vue";
import otelPage from "@/mainPages/OtelPage.vue";
import turPage from "@/mainPages/TurPage.vue";
import userPage from "@/mainPages/UserPage.vue";
import adminPage from "@/mainPages/AdminPage.vue";

export default {
  name: "LayoutDefault",

  components: {
    loginPage,
    otelPage,
    turPage,
    userPage,
    adminPage,
  },
  data() {
    return {
      user: null,
      isLogged: false,
      userType: null,
    };
  },
  methods: {
    eventLogin: function (value) {
      this.isLogged = value[0];
      this.userType = value[1];
      this.user = value[2];
    },
    logout: function(){
      this.isLogged = false;
      this.userType = null;
      this.user = null;

    }
  },
};
</script>

<style>
.menu{
  width: 15%;
}
.page {
  padding: 50px 100px;
  width: 85%;
}
@media (max-width: 1000px) {
.menu{
  width: 100%;
}
  .page {
    padding: 20px;
    width: 100%;
  }
}
</style>