import { initializeApp } from "firebase/app";
import {
  getFirestore,
  doc,
  updateDoc,
  setDoc,
  collection,
  getDocs,
  deleteDoc,
  getDoc,
  arrayUnion,
  arrayRemove,
} from "firebase/firestore";
import {
  getAuth,
  signInWithEmailAndPassword,
  updateProfile,
} from "firebase/auth";
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

const firebaseConfig = {
  apiKey: "AIzaSyDyYEYCJwCrcaMJHozyzrs7X5fI0xu3dK0",
  authDomain: "odev-3fbbf.firebaseapp.com",
  projectId: "odev-3fbbf",
  storageBucket: "odev-3fbbf.appspot.com",
  messagingSenderId: "1082714995288",
  appId: "1:1082714995288:web:0b40243c36a7caff13682d"
};


const firebaseApp = initializeApp(firebaseConfig);
const auth = getAuth(firebaseApp);
const storage = getStorage(firebaseApp);
const db = getFirestore(firebaseApp);

export const readCookie= function (cookieName) {
  var b = ""
  const a = document.cookie
    .split("; ")
    .find((row) => row.startsWith(`${cookieName}=`));
  if (a) {
    b = a.split("=")[1];
  }
  return b
}
export const signSite = function (email, password) {
  return signInWithEmailAndPassword(auth, email, password);
};

export const upProfile = function (typ) {
  return updateProfile(auth.currentUser, {
    displayName: typ,
  });
};
export const addProfile = function (mainType, email, password, type) {
  const ref = doc(db, mainType, email);
  return updateDoc(ref, { email: email, password: password, type: type });
};
export const setProfile = function (mainType, email, password, type) {
  const ref = doc(db, mainType, email);
  return setDoc(ref, { email: email, password: password, type: type });
};
export const getProfiles = function (mainType) {
  return getDocs(collection(db, mainType));
};
export const getProfile = function (mainType, email) {
  const docRef = doc(db, mainType, email);
  return getDoc(docRef);
};
export const delProfiles = function (mainType, email) {
  return deleteDoc(doc(db, mainType, email));
};

export const uploadImg = function (file, path) {
  const storageRef = ref(storage, path);
  return uploadBytes(storageRef, file);
};
export const loadImg = function (path) {
  const storageRef = ref(storage, path);
  return getDownloadURL(storageRef);
};

export const setImg = function (user, path) {
  const storageRef = doc(db, user, "imgs");
  return setDoc(storageRef, { path: [path] });
};
export const addImg = function (user, path) {
  const storageRef = doc(db, user, "imgs");
  return updateDoc(storageRef, { path: arrayUnion(path) });
};
export const remImg = function (user, path) {
  const storageRef = doc(db, user, "imgs");
  return updateDoc(storageRef, { path: arrayRemove(path) });
};
export const getImgs = function (user) {
  const docRef = doc(db, user, "imgs");
  return getDoc(docRef);
};

export const addText = function (user, textType, text) {
  const storageRef = doc(db, user, textType);
  return updateDoc(storageRef, { text: arrayUnion(text) });
};
export const remText = function (user, textType, text) {
  const storageRef = doc(db, user, textType);
  return updateDoc(storageRef, { text: arrayRemove(text) });
};
export const setTextList = function (user, textType, text) {
  const ref = doc(db, user, textType);
  return setDoc(ref, { text: [text] });
};
export const uppText = function (user, textType, text) {
  const ref = doc(db, user, textType);
  return updateDoc(ref, { text: text });
};
export const setText = function (user, textType, text) {
  const ref = doc(db, user, textType);
  return setDoc(ref, { text: text });
};
export const getText = function (user, textType) {
  const docRef = doc(db, user, textType);
  return getDoc(docRef);
};

export const setPrice = function (user, type, price) {
  const storageRef = doc(db, user, type);
  return setDoc(storageRef, { prices: [price] });
};
export const addPrice = function (user, type, price) {
  const storageRef = doc(db, user, type);
  return updateDoc(storageRef, { prices: arrayUnion(price) });
};
export const remPrice = function (user, type, price) {
  const storageRef = doc(db, user, type);
  return updateDoc(storageRef, { prices: arrayRemove(price) });
};
export const getPrices = function (user, type) {
  const docRef = doc(db, user, type);
  return getDoc(docRef);
};
