<template>
  <div class="page">
    <div class="adduser">
      <q-input v-model="kupon" label="Kupon Kodu" />

      <q-input
        v-if="kuponType == 'Oran'"
        v-model="value"
        type="number"
        label="Miktar"
      />
      <q-input
        v-if="kuponType == 'Miktar'"
        v-model="value"
        type="number"
        label="Miktar"
      />
      <q-input v-if="kuponType == 'Sil'" disable />

      <q-radio v-model="kuponType" val="Oran" label="Oran" />
      <q-radio v-model="kuponType" val="Miktar" label="Miktar" />
      <q-radio v-model="kuponType" val="Sil" label="Sil" />

      <q-btn
        class="right"
        color="white"
        text-color="black"
        label="Ekle / Güncelle"
        @click="addThing"
      />
    </div>

    <q-table title="Kuponlar" :rows="rows" :columns="columns" row-key="name" />
  </div>
</template>

<script>
import {
  addPrice,
  setPrice,
  getPrices,
  remPrice,
  readCookie,
} from "@/services.js";
import { ref } from "@vue/reactivity";

const columns = [
  {
    name: "kupon",
    label: "Kupon",
    align: "left",
    field: (row) => row.kupon,
    sortable: true,
  },
  { name: "pass", label: "Miktar", field: "value", sortable: true },
  {
    name: "type",
    label: "Tür",
    field: "type",
    align: "center",
    sortable: true,
  },
];

export default {
  props: ["user"],
  data() {
    return {
      page: "kupon",
      kupon: null,
      value: ref(null),
      kuponType: "Oran",
      columns: columns,
      rows: [],
    };
  },
  methods: {
    addThing: function () {
      const price = {
        kupon: this.kupon,
        value: Number(this.value),
        type: this.kuponType,
      };
      if (
        (this.kupon != null) &
        (this.value != 0) &
        (typeof Number(this.value) == typeof 1)
      ) {
        if (this.kuponType != "Sil") {
          this.rows.forEach((element) => {
            if (element.kupon == this.kupon) {
              this.remover();
            }
          });
          addPrice(this.user, this.page, price)
            .then(() => {
              this.load();
            })
            .catch(() => {
              setPrice(this.user, this.page, price).then(() => {
                this.load();
              });
            });
        } else {
          this.remover();
        }
      }
    },
    load: function () {
      getPrices(this.user, this.page).then((res) => {
        if (res.get("prices")) {
          this.rows = res.get("prices");
        }
      });
    },
    remover: function () {
      for (var i in this.rows) {
        console.log(this.kupon, " ", this.rows.at(i).kupon);
        if (this.rows.at(i).kupon == this.kupon) {
          remPrice(this.user, this.page, this.rows.at(i)).then(() => {
            this.load();
          });
        }
      }
    },
  },
  watch: {
    value() {
      document.cookie = `value=${this.value}; SameSite=Lax; Secure`;
      if ((this.value > 100) & (this.kuponType == "Oran")) {
        this.value = 100;
      }
    },
    kupon() {
      document.cookie = `kupon=${this.kupon}; SameSite=Lax; Secure`;
    },
    kuponType() {
      if ((this.value > 100) & (this.kuponType == "Oran")) {
        this.value = 100;
      }
    },
  },
  mounted() {
    this.load();
    this.value = readCookie("value");
    this.kupon = readCookie("kupon");
  },
};
</script>


<style scoped>
.adduser {
  display: flex;
  flex-wrap: wrap;
}
.adduser > *:not(.q-radio) {
  display: block;
  width: 100%;
}
.q-radio {
  width: 33%;
}
.right {
  margin-right: 0px;
  margin-left: auto;
}
</style>a 