<template>
  <div class="row">
    <label class="col-3" for="user"> Kullanıcı Adı: </label>
    <input
      class="col-9"
      type="text"
      name="user"
      id="user"
      v-model="user"
    /><br />
    <label class="col-3" for="pass"> Şifre: </label>
    <input class="col-9" type="password" name="pass" id="pass" v-model="pass" />
    <div class="row full-width wrap">
      <button class="col self-end offset-8" @click="eventLogin">Login</button>
    </div>
  </div>
</template>
<script>
import { signSite, getProfile } from "@/services.js";

export default {
  emits: ["eventLogin"],
  data() {
    return {
      user: null,
      pass: null,
    };
  },
  methods: {
    eventLogin: function () {
      if ((this.user != null) & (this.pass != null)) {
        this.getProfileEvent();
      }
    },
    getProfileEvent: function (jobType = "user") {
      getProfile(jobType, this.user).then((res) => {
        if (res.exists()) {
          this.checkProfileEvent(res)
        } else {
          this.getProfileEvent("admin");
        }
      });
    },
    checkProfileEvent: function(res){
      if(this.pass==res.get("password")){
        this.$emit("eventLogin",[true, res.get("type"), res.get("email")])
      }
    }
  },
  mounted() {
    signSite("admin@admin.co", "123456");
  },
};
</script>
<style scoped>
div {
  margin: auto;
}
</style>