<template>
  <div class="page">
    <div class="upload">
      <q-file v-model="file" accept="image/*" label="Resim">
        <template v-slot:prepend>
          <q-icon name="attach_file" />
        </template>
      </q-file>
      <q-btn color="white" text-color="black" label="Yükle" @click="upload">
      </q-btn>
    </div>

    <div>
      <q-carousel
        v-if="done"
        v-model="slide"
        transition-prev="slide-right"
        transition-next="slide-left"
        swipeable
        animated
        control-color="primary"
        navigation
        padding
        arrows
        height="300px"
        class="bg-grey-1 shadow-2 rounded-borders"
      >
        <q-carousel-slide
          class="column no-wrap"
          v-for="item in Math.ceil(Object.keys(imgs).length / 2)"
          :key="item"
          :name="item"
        >
          <div
            class="
              row
              fit
              justify-start
              items-center
              q-gutter-xs q-col-gutter
              no-wrap
            "
          >
            <q-img
              class="rounded-borders col-6 full-height"
              :src="imgs[(item - 1) * 2]"
            >
              <div class="absolute-bottom text-subtitle1 text-center">
                <q-btn
                  flat
                  class="glossy"
                  style="width: 100%"
                  color="primary"
                  label="SİL"
                  @click="remImgFunc((item - 1) * 2)"
                />
              </div>
            </q-img>
            <q-img
              v-if="imgs[(item - 1) * 2 + 1]"
              class="rounded-borders col-6 full-height"
              :src="imgs[(item - 1) * 2 + 1]"
            >
              <div class="absolute-bottom text-subtitle1 text-center">
                <q-btn
                  flat
                  class="glossy"
                  style="width: 100%"
                  color="primary"
                  label="SİL"
                  @click="remImgFunc((item - 1) * 2 + 1)"
                />
              </div>
            </q-img>
          </div>
        </q-carousel-slide>
      </q-carousel>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import {
  uploadImg,
  addImg,
  setImg,
  getImgs,
  loadImg,
  remImg,
} from "@/services.js";
export default {
  props: {
    user: String,
  },
  data() {
    return {
      slide: ref(1),
      file: null,
      imgurl: null,
      img: null,
      paths: "",
      imgs: {},
      imgl: 0,
      done: false,
    };
  },
  methods: {
    upload: function () {
      const ext = this.file.name.split(".").at(-1);
      const path = `${this.user}/${this.imgl}.${ext}`;
      uploadImg(this.file, path);
      addImg(this.user, path)
        .then(() => {
          this.load();
        })
        .catch(() => {
          setImg(this.user, path).then(() => {
            this.load();
          });
        });
      this.imgl++;
    },
    load: function () {
      this.imgs = {};
      this.done = false;
      getImgs(this.user).then((res) => {
        if (res.exists) {
          this.paths = res.get("path");
          if (this.paths) {
            this.imgl = this.paths.length;
            for (var i = 0; i < this.paths.length; i++) {
              this.loadImg(this.paths[i], i);
            }
          }
        }
      });
    },
    loadImg: function (path, index) {
      loadImg(path)
        .then((res) => {
          this.imgs[index] = res;

          this.done = true;
        })
        .catch(() => {
          this.loadImg(path, index);
        });
    },
    remImgFunc: function (imgIndex) {
      remImg(this.user, this.paths[imgIndex]).then(() => {
        this.load();
      });
      this.slide = ref(1);
    },
  },
  watch: {
  },
  mounted() {
    this.load();
  },
};
</script>

<style scoped>
.q-file {
  width: 75%;
}
.upload {
  display: flex;
  flex-wrap: wrap;
}
.upload .q-field {
  width: 75%;
}
.upload .q-btn {
  width: 25%;
}
</style>