<template>
  <div class="page">
    <div style="display: flex; flex-wrap: wrap">
      <q-input v-model="talep" label="Talepler" style="width: 80%" />
      <q-btn
        label="Gönder"
        outline
        color="purple"
        style="width: 20%"
        @click="saveText('talep')"
      />
    </div>
    <div>
      <editor v-model="text" />
    </div>

    <div>
      <q-table
        title="Talepler"
        :rows="rows"
        :columns="columns"
        row-key="name"
      />
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import editor from "./editor.vue";
import {
  addText,
  setTextList,
  getText,
  remText,
  readCookie,
} from "@/services.js";

const columns = [
  {
    name: "user",
    label: "Talep İsmi",
    align: "left",
    field: (row) => row.header,
    sortable: true,
  },
  {
    name: "type",
    label: "Talep  Durumu",
    field: "type",
    align: "center",
    sortable: true,
  },
];
export default {
  components: {
    editor,
  },
  props: ["user"],

  data() {
    return {
      talep: null,
      text: ref(""),
      rows: [],
      columns: columns,
    };
  },
  methods: {
    saveText: function () {
      const ob = {
        user: this.user,
        header: this.talep,
        text: this.text,
        type: "açık",
      };
      this.rows.forEach((element) => {
        if (element.header == this.talep) {
          this.remover();
        }
      });
      if (this.talep != "") {
        addText("talepler", "talep", ob).catch(() => {
          setTextList("talepler", "talep", ob).ca;
        });

        addText(this.user, "talep", ob)
          .then(() => {
            this.loader();
          })
          .catch(() => {
            setTextList(this.user, "talep", ob).then(() => {
              this.loader();
            });
          });
      }
    },
    loader: function () {
      getText(this.user, "talep").then((res) => {
        if (res.get("text")) {
          this.rows = ref(res.get("text"));
        }
      });
    },
    remover: function () {
      for (var i in this.rows) {
        console.log(i)
        if (this.rows.at(i).header == this.talep) {
          remText("talepler", "talep", this.rows.at(i))
          remText(this.user, "talep", this.rows.at(i)).then(() => {
            this.loader();
          });
        }
      }
    },
  },

  watch: {
    talep() {
      document.cookie = `talep=${this.talep}; SameSite=Lax; Secure`;
    },
  },
  mounted() {
    this.loader();
    this.talep = readCookie("talep");
  },
};
</script>

