<template>
  <MenuComp :menuItems="menuItems" @choosePage="choosePage" @logout="logout"></MenuComp>
  <KulComp v-if="choseMenu=='Kullanıcılar'" :user="user"></KulComp>
  <CalComp v-if="choseMenu=='Çalışanlar'" :user="user"></CalComp>
  <TalepComp v-if="choseMenu=='Talepler'" :user="user"></TalepComp>
</template>
<script>
import MenuComp from "@/components/Menu.vue";
import KulComp from "@/components/Kullanıcılar.vue";
import CalComp from "@/components/Calisanlar.vue";
import TalepComp from "@/components/TalepUser.vue";
export default {
  components: {
    MenuComp,
    KulComp,
    CalComp,
    TalepComp,
  },
  props: {
    user:String
  },
  emits: ["logout"],
  data() {
    return {
      menuItems: [
        "Kullanıcılar",
        "Çalışanlar",
        "Talepler",
      ],
      choseMenu: "Kullanıcılar  ",
    };
  },
  methods: {
    choosePage: function (value) {
      this.choseMenu = value;
    },
    logout: function(){
      this.$emit("logout")
    }
  },
};
</script>