<template>
  <div class="page">
    <div class="left">
      <h4>Gidilen Yerler</h4>
      <editor v-model="places" />
      <q-btn
        label="Kaydet"
        outline
        color="purple"
        @click="saveText('places')"
      />
    </div>
    <div class="right">
      <h4>Açıklma</h4>
      <editor v-model="desc" />

      <q-btn label="Kaydet" outline color="purple" @click="saveText('desc')" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import editor from "./editor.vue";
import { uppText, setText, getText } from "@/services.js";

export default {
  components: {
    editor,
  },
  props: ["user"],
  data() {
    return {
      places: ref(""),
      desc: ref(""),
    };
  },
  methods: {
    saveText: function (type) {
      console.log(this[type]);
      uppText(this.user, type, this[type]).catch(() => {
        setText(this.user, type, this[type]);
      });
    },
  },
  mounted() {
    getText(this.user, "places").then((res) => {
      if (res.get("text")) {
        this.places = ref(res.get("text"));
      }
    });
    getText(this.user, "desc").then((res) => {
      if (res.get("text")) {
        this.desc = ref(res.get("text"));
      } 
    });
  },

};
</script>

<style scoped>
.q-btn {
  width: 100%;
}
.page {
  display: flex;
  flex-wrap: wrap;
}
.left {
  width: 50%;
}
.right {
  width: 50%;
}
@media (max-width: 1000px) {
  .left {
    width: 100%;
  }
  .right {
    width: 100%;
  }
}
</style>