<template>
  <div class="menu">
    <div v-for="menuItem in menuItems" :key="menuItem">
      <q-btn
        v-if="(typeof menuItem == typeof 'a') & true"
        color="white"
        text-color="black"
        :label="menuItem"
        @click="choosePage(menuItem)"
      />

      <q-expansion-item
        class="wi"
        v-if="typeof menuItem == typeof { a: 'v' }"
        dense
        :label="Object.keys(menuItem)[0]"
      >
        <q-btn
          class="row"
          v-for="item in menuItem[Object.keys(menuItem)[0]]"
          :key="item"
          @click="choosePage(item)"
          color="white"
          text-color="black"
          :label="item"
        />
      </q-expansion-item>
    </div>
    <div>
      <q-btn
        color="white"
        text-color="black"
        label="Çıkış"
        @click="logout(menuItem)"
      />
    </div>
  </div>
</template>

<style>
</style>

<script>
export default {
  props: {
    menuItems: Array,
  },
  emits: ["choosePage", "logout"],
  methods: {
    choosePage: function (value) {
      this.$emit("choosePage", value);
    },
    logout: function () {
      this.$emit("logout");
    },
  },
};
</script>

