<template>
  <div class="page">
    <div style="display: flex; flex-wrap: wrap">
      <q-input v-model="talep" label="Talep" style="width: 60%" />
      <q-btn
        label="Oku"
        outline
        color="purple"
        style="width: 20%"
        @click="oku"
      />
      <q-btn
        label="Kapa"
        outline
        color="purple"
        style="width: 20%"
        @click="kapa"
      />
    </div>
    <div>
      <q-table
        title="Talepler"
        :rows="rows"
        :columns="columns"
        row-key="name"
      />
    </div>

    <div>
      <editor v-if="text" v-model="text" />
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import editor from "./editor.vue";
import {
  getText,
  remText,
  addText,
  setTextList,
  readCookie,
} from "@/services.js";

const columns = [
  {
    name: "user",
    label: "Talep İsmi",
    align: "left",
    field: (row) => row.header,
    sortable: true,
  },
  {
    name: "asd",
    label: "Kullanıcı",
    field: "user",
    align: "center",
    sortable: true,
  },
  {
    name: "type",
    label: "Talep  Durumu",
    field: "type",
    align: "center",
    sortable: true,
  },
];
export default {
  components: {
    editor,
  },
  props: ["user"],

  data() {
    return {
      talep: null,
      text: ref(""),
      rows: [],
      columns: columns,
    };
  },
  methods: {
    oku: function () {
      for (var i in this.rows) {
        if (this.rows[i].header == this.talep) {
          this.text = this.rows[i].text;
        }
      }
    },
    kapa: function () {
      for (var i in this.rows) {
        if (this.rows[i].header == this.talep) {
          const old = this.rows[i];
          const or = {
            user: this.rows[i].user,
            header: this.talep,
            text: this.text,
            type: "kapalı",
          };
          this.text = null;
          remText(or.user, "talep", old).then(() => {
            addText(or.user, "talep", or)
              .then(() => {
                this.loader();
              })
              .catch(() => {
                setTextList(or.user, "talep", or).then(() => {
                  this.loader();
                });
              });
          });
          remText("talepler", "talep", old).then(() => {
            addText("talepler", "talep", or)
              .then(() => {
                this.loader();
              })
              .catch(() => {
                setTextList("talepler", "talep", or).then(() => {
                  this.loader();
                });
              });
          });
        }
      }
    },
    loader: function () {
      getText("talepler", "talep").then((res) => {
        if (res.get("text")) {
          this.rows = ref(res.get("text"));
        }
      });
    },
  },
  watch: {
    talep() {
      document.cookie = `talepUser=${this.talep}; SameSite=Lax; Secure`;
    },
  },
  mounted() {
    this.loader();
    this.talep = readCookie("talepUser");
  },
};
</script>

